.catalog {
  width: 61%;

  display: block;

  padding: 200px 0 62px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 80.21%;
    //min-width: auto;

    padding: 80px 0 55px;
    margin: 0 auto;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    padding: 40px 16px 20px;
    box-sizing: border-box;
  }
}

.catalog__title {
  width: 358px;

  font-size: 55px;
  line-height: 80px;
  font-weight: 600;
  letter-spacing: -1.25px;

  color: $catalog-title-color;

  position: relative;

  padding: 0 0 34px 0;
  margin: 0 0 30px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    font-size: 30px;
    line-height: 45px;
    font-weight: 600;
    text-align: center;

    display: block;

    padding: 0;
    margin: 0 auto 20px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    font-size: 24px;
    line-height: 36px;
    font-weight: 600;
    text-align: center;
    letter-spacing: 0;

    display: block;

    padding: 0;
    margin: 0 0 20px;
  }

  &::before {
    content: "";

    width: 228.51px;
    height: 4px;

    position: absolute;
    bottom: 0;
    left: 0;

    background: $accent;

    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      display: none;
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      display: none;
    }
  }
}

.catalog__text {
  width: 329px;

  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  letter-spacing: -0.47px;

  color: $catalog-text-color;

  margin: 0 0 55px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    text-align: center;

    margin: 0 auto 40px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    text-align: center;

    margin: 0 auto 20px;
    box-sizing: border-box;
  }
}

.catalog__list {
  width: 100%;

  list-style: none;

  display: flex;
  flex-flow: row wrap;
  row-gap: 100px;

  padding: 0;
  margin: 0;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    gap: 40px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    gap: 35px 0;
  }
}

.catalog__item {
  width: 30%;
  height: 430px;

  margin: 0 5% 100px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 46.75%;
    height: 425px;

    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    height: 425px;

    margin: 0;
  }
}

.catalog__item:nth-child(5n - 2) {
  margin: 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    margin: 0;
  }
}

.catalog__item:nth-child(5n - 1) {
  margin: 0 10% 100px 15%;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    margin: 0;
  }
}

.catalog__item:nth-child(5n) {
  margin: 0 auto 0 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    margin: 0 auto;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    margin: 0;
  }
}

.catalog__list:nth-last-child(1) .catalog__item {
  margin-bottom: 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    margin: 0 auto;
  }
}

.catalog__link {
  display: block;

  position: relative;

  &::before {
    content: "смотреть подробнее";

    display: none;

    width: 266px;
    height: 77px;

    font-family: inherit;
    font-size: 14px;
    line-height: 17.07px;
    letter-spacing: 2px;
    text-align: center;

    position: absolute;
    inset: 0;

    border: 1px solid $catalog-item-before-border-color;

    color: $catalog-item-before-text-color;

    padding: 30px 0;
    margin: auto;
    box-sizing: border-box;

    z-index: 1;

    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      display: none;
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      display: none;
    }
  }

  &::after {
    content: "";

    display: none;

    position: absolute;
    inset: 0;

    background: black;
    opacity: 60%;
  }

  &:focus {
    outline: 5px solid rgb(0, 255, 98);
    outline-offset: -5px;
  }
}

.catalog__item:hover .catalog__link::before,
.catalog__item:hover .catalog__link::after {
  display: block;
}

.catalog__item:hover .catalog__link {
  background: $catalog-item-hover;
}

.catalog__link-picture,
.catalog__link-image {
  width: 100%;
  height: 430px;

  display: block;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    height: 425px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    height: 425px;
  }
}

.catalog__link-text {
  width: 61.43%;

  font-size: 24px;
  line-height: 29.26px;
  font-weight: 500;
  letter-spacing: -0.41px;
  text-align: center;
  overflow-x: hidden;

  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%);

  color: $catalog-item-text-color;
  background: $catalog-item-background-color;

  padding: 16.56px 0 17.44px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 74.65%;

    top: 85.8%;

    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 74.65%;

    top: 85.5%;

    margin: 0;
  }
}
