.popup-buy-tour-success {
  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  background: $popup-buy-tour-container-background;

  position: fixed;
  inset: 0;

  margin: auto;

  z-index: 1;
}

.popup-buy-tour-success--hide {
  display: none;
}

.popup-buy-tour-success__window {
  width: 728px;
  max-width: 100%;
  height: 444px;

  position: static;

  display: flex;
  align-items: center;

  background: $popup-buy-tour-form-background;
}

.popup-buy-tour__form {
  display: block;
}

.popup-buy-tour-success__text {
  width: 399px;

  font-family: inherit;
  font-size: 25px;
  line-height: 42.5px;
  text-align: center;

  background: $popup-buy-tour-form-background;

  padding: 0;
  margin: 0 auto;
}

.popup-buy-tour-success__button-close {
  width: 22px;
  height: 22px;

  position: absolute;
  top: 22px;
  left: calc(100% - 42px);

  background: transparent;

  border: none;

  padding: 0;
  margin: 0;
}

.popup-buy-tour-success__button-close svg {
  width: 22px;
  height: 22px;
}
