.corporate {
  width: 33.3%;

  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    padding: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    padding: 29px 0 10px;
  }
}

.corporate__title {
  width: 82.05%;

  font-size: 18px;
  line-height: 32px;
  font-weight: 600;

  color: $footer-title-color;

  margin: 0 0 20px auto;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    text-align: center;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    text-align: center;
  }
}

.corporate__text {
  width: 82.05%;
  font-size: 14px;
  line-height: 23.8px;
  font-weight: 400;

  color: $footer-text-color;

  margin: 0 0 0 auto;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    text-align: center;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    text-align: center;
  }
}
