.footer {
  width: 61%;
  min-width: 1024px;

  grid-row: 3 / 4;

  display: flex;
  justify-content: space-between;

  padding: 162px 0 74px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    min-width: auto;
    height: auto;

    grid-row: 3 / 4;

    flex-flow: row wrap;

    padding: 81px 21px 54px;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    height: auto;
    min-width: auto;

    grid-row: 3 / 4;

    flex-flow: row wrap;

    padding: 30px 16px 16px;
    box-sizing: border-box;
  }
}
