.navigation {
  width: 100%;
  height: auto;

  display: flex;
  flex-flow: row wrap;

  padding: 36px 0 32px;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    height: auto;

    justify-content: space-between;
    align-items: center;

    padding: 0 0 0 21px;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    height: auto;

    justify-content: space-between;
    align-items: center;

    padding: 0;
  }
}

.navigation--open {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    min-height: 568px;

    justify-content: space-between;
    align-items: center;

    padding: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    min-height: 568px;

    justify-content: space-between;
    align-items: center;

    padding: 0;
  }
}

.navigation--nojs {
  width: 100%;
  height: 100px;

  display: flex;

  padding: 36px 0 32px;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;

    padding: 0;
    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;

    padding: 0;
    margin: 0;
  }
}

.navigation__logo {
  width: 57px;
  height: 22px;

  padding: 2px 0 0 2px;
  margin: 0 auto 0 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 44px;
    height: 17px;

    padding: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 44px;
    height: 17px;

    padding: 2px 0 0 16px;
  }
}

.navigation__logo--nojs {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 44px;
    height: 17px;

    padding: 20px 0;
    margin: 0 auto;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 44px;
    height: 17px;

    padding: 20px 0;
    margin: 0 auto;
  }
}

.navigation__logo--hide {
  display: none;
}

.navigation__list {
  height: max-content;

  font-weight: 500;
  text-align: center;
  list-style: none;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0;
  margin: 0 6% 0 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    display: none;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    display: none;
  }
}

.navigation__list--open {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    font-weight: 500;
    text-align: center;
    list-style: none;

    display: flex;
    flex-flow: column;

    padding: 134px 0 0 0;
    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    font-weight: 500;
    text-align: center;
    list-style: none;

    display: flex;
    flex-flow: column;

    padding: 134px 0 0 0;
    margin: 0;
  }
}

.navigation__list--nojs {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    display: flex;
    flex-flow: row wrap;

    padding: 0;
    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    display: flex;
    flex-flow: row wrap;

    padding: 0;
    margin: 0;
  }
}

.navigation__item {
  font-size: 14px;
  line-height: 17.07px;
  font-weight: 500;

  color: $navigation-text-color;

  margin: 0 40px 0 0;

  &::before {
    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      content: "";

      width: 25px;
      height: 25px;

      position: absolute;
      top: 50%;
      right: 12.5px;

      transform: translateY(-50%);


      background-image: url("../img/menu-item-nojs.svg");
    }
  }

  &:last-child {
    margin: 0;
  }
}

.navigation__item--nojs {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    font-size: 14px;
    line-height: 17.07px;
    font-weight: 500;
    text-align: start;

    position: relative;

    color: $navigation-text-color;
    background-color: #fbf8f8;

    padding: 20px;
    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    font-size: 14px;
    line-height: 17.07px;
    font-weight: 500;
    text-align: start;

    position: relative;

    color: $navigation-text-color;
    background-color: #fbf8f8;

    padding: 20px;
    margin: 0;
  }

  &::before {
    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      content: "";

      width: 25px;
      height: 25px;

      position: absolute;
      top: 50%;
      right: 12.5px;

      transform: translateY(-50%);


      background: url("../img/menu-item-nojs.svg");
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      content: "";

      width: 25px;
      height: 25px;

      position: absolute;
      top: 50%;
      right: 12.5px;

      transform: translateY(-50%);


      background: url("../img/menu-item-nojs.svg");
    }
  }
}

.navigation__item--open {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    font-size: 14px;
    line-height: 17.07px;
    font-weight: 500;
    text-align: start;

    position: relative;

    color: $navigation-text-color;
    background-color: #fbf8f8;

    margin: 0 0 30px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    font-size: 14px;
    line-height: 17.07px;
    font-weight: 500;
    text-align: start;

    position: relative;

    color: $navigation-text-color;
    background: #fbf8f8;

    margin: 0 0 30px;
  }

  &::before {
    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      content: "";

      width: 25px;
      height: 25px;

      position: absolute;
      top: 50%;
      right: 12.5px;

      transform: translateY(-50%);


      background: url("../img/menu-item-nojs.svg");
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      content: "";

      width: 25px;
      height: 25px;

      position: absolute;
      top: 50%;
      right: 12.5px;

      transform: translateY(-50%);


      background: url("../img/menu-item-nojs.svg");
    }
  }
}

.navigation__link {
  text-decoration: none;

  display: block;

  color: $navigation-text-color;

  padding: 6px 0 9px;

  &:focus {
    outline: 1px solid rgb(0, 255, 98);
    outline-offset: -1px;
  }
}

.navigation__link--open {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    text-decoration: none;

    display: block;

    color: #48494d;

    padding: 17px 16px;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    display: block;

    text-decoration: none;

    color: #48494d;

    padding: 17px 16px;
    box-sizing: border-box;
  }
}

.navigation__item:hover .navigation__link {
  color: $accent;
}

.navigation__phone-container {
  width: 230px;

  font-size: 18px;
  line-height: 32px;
  font-weight: 400;
  text-align: center;

  display: flex;
  justify-content: space-between;

  color: $navigation-phone-color;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    display: none;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    display: none;
  }
}

.navigation__phone-container .navigation__link {
  padding: 0;
}

.navigation__phone-container:hover .navigation__link,
.navigation__phone-container:hover path {
  text-decoration: underline;

  color: $accent;
  stroke: $accent;
}

.navigation__phone-image {
  margin: 0 6px 0 0;
}

.navigation__menu-open {
  display: none;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 320px;
    min-height: 568px;
    height: auto;

    display: flex;
    flex-flow: row wrap;

    position: relative;

    background-color: white;

    padding: 134px 0 0;
    margin: 0 0 0 auto;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 320px;
    min-height: 568px;
    height: auto;

    display: flex;
    flex-flow: row wrap;

    position: relative;

    background: white;

    padding: 134px 0 0;
    margin: 0 0 0 auto;
    box-sizing: border-box;
  }
}

.navigation__menu-close-button {
  display: none;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 72px;
    height: 50px;
    display: block;

    border: none;

    background: $accent;

    padding: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 50px;
    height: 50px;

    display: block;

    border: none;

    background: $accent;

    padding: 0;
  }
}

.navigation__menu-close-button--hide {
  display: none;
}

.navigation__menu-close-button--nojs {
  display: none;
}

.navigation__menu-open-button {
  display: none;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 50px;
    height: 52px;

    display: block;

    position: absolute;
    top: 0;
    right: 0;

    border: none;

    background: transparent;

    padding: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 50px;
    height: 52px;

    display: block;

    position: absolute;
    top: 0;
    right: 0;

    border: none;

    background: transparent;

    padding: 0;
  }
}

.navigation__menu-open-button--hide {
  display: none;
}

.navigation__menu-close-image {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 30px;
    height: 30px;

    background: transparent;

    display: block;
    margin: auto;
  }
}

.navigation__menu-open-image {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 50px;
    height: 52px;
  }
}

.navigation__menu--hide {
  display: none;
}

.navigation__menu-open-list {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    list-style: none;

    padding: 0;
    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    list-style: none;

    padding: 0;
    margin: 0;
  }
}

