.place {
  width: 61%;
  min-width: 1024px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  position: relative;

  background: url("../img/place-greece-desktop.png") no-repeat 470px 65px;
  background-size: 430px 596px;

  padding: 65px 0 136px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/place-greece-desktop@2x.png");
  }

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    min-width: auto;
    width: 100%;

    background: none;

    display: block;

    padding: 16px 0 40px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    min-width: auto;

    display: block;

    background: none;

    padding: 0;
    margin: 0;
  }
}

.place__more {
  width: 31.62%;

  margin: 0 8.55% 0 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    padding: 0 0 30px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    margin: 0;

    padding: 0 0 50px;
  }
}

.place__more-title {
  font-size: 55px;
  line-height: 90px;
  font-weight: 600;
  letter-spacing: -1.25px;

  position: relative;

  color: $place-text-color;

  padding: 0 0 17px 0;
  margin: 10px 0 30px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    font-size: 30px;
    line-height: 45px;
    font-weight: 600;
    text-align: center;
    letter-spacing: 0;

    padding: 0;
    margin: 0 0 20px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    font-size: 24px;
    line-height: 36px;
    font-weight: 600;
    text-align: center;
    letter-spacing: 0;

    padding: 0;
    margin: 0 0 20px;
  }

  &::before {
    content: "";

    width: 175px;
    height: 3px;

    position: absolute;
    bottom: 0;
    left: 0;

    background: $accent;

    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      display: none;
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      display: none;
    }
  }
}

.place__more-text {
  min-width: 370px;
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  letter-spacing: -0.47px;

  color: $place-text-color;

  margin: 0 0 50px;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    text-align: center;

    padding: 0 21px;
    margin: 0 0 19px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    min-width: auto;

    text-align: center;

    padding: 0 16px;
    margin: 0 0 21px;
    box-sizing: border-box;
  }
}

.place__more-button {
  width: 250px;
  height: 75px;

  font-family: inherit;
  font-size: 12px;
  line-height: 14.63px;
  font-weight: 700;
  letter-spacing: 2px;

  color: $button-text-color;
  background: $accent;

  border: 0;

  padding: 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    height: 50px;

    font-size: 10px;
    line-height: 12.19px;
    font-weight: 500;

    display: block;

    margin: 0 auto;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 288px;
    height: 50px;

    font-size: 10px;
    line-height: 12.19px;
    font-weight: 500;
    letter-spacing: 2px;

    display: block;

    margin: 0 auto 0;
  }

  &:focus {
    outline: 3px solid lightgreen;
    outline-offset: -3px;
  }
}

.place__more-button:hover {
  background-color: $accent-hover;
}

.place__review {
  width: 382px;

  padding: 0;
  margin: 0;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    height: 447px;

    background: url("../img/place-greece-tablet.png") no-repeat 0 0;
    background-size: 100% 415px;

    padding: 80px 0 0 0;
    margin: 0 auto;
    box-sizing: border-box;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/place-greece-tablet@2x.png");
    }
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    height: 481px;

    background: url("../img/place-greece-mobile.png") no-repeat 100% 0;
    background-size: 100% 415px;

    padding: 50px 0 34px;
    margin: 0 auto;
    box-sizing: border-box;

    @media (min-resolution: 2dppx) {
      background: url("../img/place-greece-mobile@2x.png") 0 0;
    }
  }
}

.place__review-container {
  width: 100%;
  height: 367px;

  background-color: $review-background-color;

  padding: 39px 30px;
  margin: 0;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 382px;

    padding: 30px 30px 39px;
    margin: 0 auto;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 268px;
    min-height: 431px;

    padding: 32px 20px 34px;
    margin: 0 auto;
    box-sizing: border-box;
  }
}

.place__review-title {
  font-size: 30px;
  line-height: 36.57px;
  font-weight: 500;
  letter-spacing: -0.69px;

  color: $place-title-color;

  position: relative;

  padding: 0 0 14px 0;
  margin: 0 0 22px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    padding: 0 0 14px 0;
    margin: 0 0 20px 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    font-size: 20px;
    line-height: 24.38px;
    font-weight: 500;
    text-align: center;
    letter-spacing: -0.69px;

    padding: 0 0 12px 0;
    margin: 0 0 20px 0;
  }

  &::before {
    content: "";

    width: 68px;
    height: 1px;

    position: absolute;
    bottom: 0;
    left: 0;

    background: $accent;

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      width: 25.37%;

      left: 50%;

      transform: translate(-50%);
    }
  }
}

.place__review-text {
  width: 322px;

  font-size: 14px;
  line-height: 28px;
  font-weight: 400;
  letter-spacing: -0.41px;

  color: $place-text-color;

  margin: 0 0 29px;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    padding: 0;
    margin: 0 0 31px 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    font-size: 14px;
    line-height: 28px;
    font-weight: 400;
    text-align: center;
    letter-spacing: -0.41px;

    padding: 0;
    margin: 0 0 17px 0;
  }
}

.place__review-author {
  display: flex;
  align-items: center;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    flex-flow: column;
  }
}

.place__review-author-image {
  width: 37.19px;
  height: 38px;

  margin: 0 15.81px 0 0;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    margin: 0 0 9px;
  }
}

.place__review-author-name {
  font-size: 16px;
  line-height: 19.5px;
  font-weight: 500;
  letter-spacing: -0.47px;

  position: relative;

  color: $place-title-color;

  padding: 9px 0;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    text-align: center;

    display: block;

    padding: 0;
  }
}
