.preview {
  width: 100%;

  background: url("../img/preview-back-desktop.png") no-repeat;
  background-size: 100% 860px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/preview-back-desktop@2x.png");
  }

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    background: url("../img/preview-back-tablet.png") no-repeat;
    background-size: 100% 344px;

    padding: 0 0 83px 0;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/preview-back-tablet@2x.png");
    }
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    background: url("../img/preview-back-mobile.png") no-repeat;
    background-size: 100% 235px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/preview-back-mobile@2x.png");
    }
  }
}

.preview__container {
  width: 412px;

  padding: 185px 0 296px;
  margin: 0 432px 0 auto;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 398px;

    padding: 60px 0 0;
    margin: 0 auto;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    padding: 83px 0 40px 0;
    margin: 0 auto;
  }
}

.preview__title {
  font-size: 55px;
  line-height: 80px;
  font-weight: 600;
  letter-spacing: -1.11px;

  color: $preview-title-color;

  margin: 0 0 30px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    font-size: 30px;
    line-height: 45px;
    font-weight: 600;
    text-align: center;
    letter-spacing: 0;

    margin: 0 0 15px 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 294px;

    font-size: 24px;
    line-height: 36px;
    font-weight: 600;
    text-align: center;
    letter-spacing: normal;

    padding: 0;
    margin: 0 auto 102px;
    box-sizing: border-box;
  }
}

.preview__text {
  width: 91.26%;
  font-size: 16px;
  line-height: 27.2px;
  font-weight: 400;
  letter-spacing: -0.47px;

  color: $preview-text-color;

  margin: 0 0 33px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    min-width: 398px;

    font-size: 16px;
    line-height: 27.2px;
    font-weight: 400;
    text-align: center;
    letter-spacing: -0.47px;

    margin: 0 0 10px 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    min-width: 288px;

    font-size: 16px;
    line-height: 27.2px;
    font-weight: 400;
    text-align: center;
    letter-spacing: -0.47px;

    color: $preview-text-color-mobile;

    margin: 0 auto 18px;
    box-sizing: border-box;
  }
}

.preview__button {
  width: 340px;
  min-height: 75px;

  font-size: 12px;
  line-height: 14.63px;
  font-weight: 700;
  letter-spacing: 2px;
  text-align: left;
  font-family: inherit;
  text-decoration: none;

  display: block;

  position: relative;

  color: $preview;
  background: $accent;

  border: none;

  padding: 30px 40px;
  box-sizing: border-box;

  cursor: pointer;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 250px;
    min-height: 50px;

    display: block;

    font-size: 10px;
    line-height: 12.19px;
    font-weight: 500;
    text-align: left;
    letter-spacing: 2px;
    text-decoration: none;

    padding: 19px 0 19px 40px;
    margin: 0 auto;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 288px;
    min-height: 50px;

    text-decoration: none;

    display: block;

    padding: 17px 0 19px 40px;
    margin: 0 auto;
    box-sizing: border-box;
  }

  &::before {
    content: "";

    width: 14px;
    height: 14px;

    position: absolute;
    top: 31px;
    right: 40px;

    background: url("../img/preview-arrow.svg");
    background-size: 14px;

    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      top: 18px;
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      top: 18px;
    }
  }

  &:focus {
    outline: 3px solid lightgreen;
    outline-offset: -3px;
  }
}

.preview__button:hover {
  background: $accent-hover;
}
