.price {
  min-width: 1024px;
  width: 61%;

  display: flex;
  justify-content: space-between;

  padding: 113px 0 140px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    min-width: auto;

    display: flex;
    flex-flow: column;

    padding: 40px 0 109px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    min-width: auto;

    display: flex;
    flex-flow: column;

    padding: 45px 16px 25px;
    box-sizing: border-box;
  }
}

.price__list {
  width: 65.81%;

  list-style: none;

  display: flex;

  padding: 0;
  margin: 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    justify-content: space-between;

    padding: 0 71px;
    margin: 0;
    box-sizing: border-box;

    order: 2;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    flex-flow: row wrap;

    padding: 0;
    margin: 0;
    box-sizing: border-box;

    order: 2;
  }
}

.price__item {
  width: 48.05%;
  min-height: 672px;

  display: flex;
  flex-flow: column;
  justify-content: space-between;

  border: 1px solid $price-item-border;
  border-radius: 6px;

  color: $price-item-text-color;

  padding: 40px 30px 30px;
  margin: 0 auto 0 0;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 46.01%;
    min-height: 629px;

    border: 3px solid $accent;
    border-radius: 0;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;

    padding: 28px 0 0;
    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    min-height: 629px;

    border: 3px solid $accent;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;

    padding: 27px 0 0;
    margin: 0 0 28px;
  }
}

.price__item:last-child {
  margin: 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    border: 1px solid $price-item-border;

    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    border: 1px solid $price-item-border;

    margin: 0;
  }
}

.price__rate {
  width: 100%;

  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;

  color: $price-item-title-color;

  margin: 0 0 40px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    display: block;

    padding: 0 20px 10px;
    margin: 0;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    display: block;

    padding: 0 20px;
    margin: 0 0 11px;
    box-sizing: border-box;
  }
}

.price__rate-name {
  font-size: 28px;
  line-height: 34px;
  font-weight: 600;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    font-size: 24px;
    line-height: 38px;
    font-weight: 600;
    letter-spacing: -1.27px;
    text-align: center;

    display: block;

    margin: 0 auto 6px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    font-size: 24px;
    line-height: 38px;
    text-align: center;

    display: block;

    margin: 0 auto 7px;
  }
}

.price__rate-cost {
  width: max-content;
  font-size: 25px;
  line-height: 34px;
  font-weight: 700;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    text-align: center;

    display: block;

    margin: 0 auto;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    text-align: center;

    display: block;

    margin: 0 auto;
  }
}

.price__text {
  width: 100%;

  font-size: 16px;
  line-height: 24px;
  font-weight: 400;

  position: relative;

  padding: 0 0 27px 0;
  margin: 0 0 30px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    text-align: center;

    padding: 0 20px 8px;
    margin: 0 0 23px 0;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    text-align: center;

    padding: 0 20px 10px;
    margin: 0 0 20px 0;
    box-sizing: border-box;
  }

  &::before {
    content: "";

    width: 100%;
    height: 1px;

    position: absolute;
    bottom: 0;
    left: 0;

    background: $price-text-border-bottom;

    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      width: 248px;

      left: 20px;
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      width: calc(100% - 40px);

      left: 20px;
    }
  }
}

.price__item:last-child .price__text {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    text-align: center;

    box-sizing: border-box;
  }
}

.price__list-description {
  list-style: none;

  display: flex;
  flex-flow: column;

  padding: 0;
  margin: 0 0 34px 0;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    padding: 0 0 0 21px;
    margin: 0 0 74px 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    padding: 0 0 0 17px;
    margin: 0 0 83px 0;
  }
}

.price__item:last-child .price__list-description {
  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    padding: 0 0 0 17px;
    margin: 0 0 72px 0;
  }
}

.price__item-description {
  width: 100%;

  font-size: 16px;
  line-height: 32px;
  font-weight: 400;

  position: relative;

  padding: 0 0 0 30px;
  margin: 0 0 12px 0;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    padding: 0 0 0 28px;
    margin: 0 0 7.5px 0;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    padding: 0 0 0 28px;
    margin: 0 0 7px 0;
    box-sizing: border-box;
  }

  &::before {
    content: "";

    width: 22px;
    height: 22px;

    position: absolute;
    top: 6px;
    left: 0;

    background: url("../img/price-jackdaw-accent.svg") no-repeat;
  }

  &:last-child {
    margin: 0;
  }
}

.price__item:hover {
  border: 3px solid $accent;
}

.price__item:hover .price__rate {
  color: $accent;
}

.price__item:hover .price__item-description::before {
  background: url("../img/price-jackdaw.svg") no-repeat;
}

.price__button {
  max-width: 310px;
  width: 100%;

  font-family: inherit;
  font-size: 12px;
  line-height: 14.63px;
  font-weight: 700;
  letter-spacing: 2px;

  color: $button-text-color;
  background: $accent;

  border: 0;

  padding: 30px 70px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    max-width: 288px;
    width: 100%;
    height: 50px;

    font-family: inherit;
    font-size: 10px;
    line-height: 12.19px;
    font-weight: 500;

    padding: 0;
    margin: 0 auto;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    max-width: 288px;
    width: 100%;
    height: 50px;

    font-family: inherit;
    font-size: 10px;
    line-height: 12.19px;
    font-weight: 500;
    letter-spacing: 2px;

    padding: 0;
    margin: 0 auto;
  }

  &:focus {
    outline: 3px solid lightgreen;
    outline-offset: -3px;
  }
}

.price__button:hover {
  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    background-color: $accent-hover;
  }
}

.price__info {
  width: 26.15%;

  margin: 110px 0 0 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    order: 1;

    margin: 0;
  }
}

.price__info-title {
  font-size: 55px;
  line-height: 90px;
  font-weight: 600;

  color: $price-info-title-color;

  position: relative;

  padding: 0 0 17px;
  margin: 0 0 30px;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    font-size: 30px;
    line-height: 45px;
    font-weight: 600;
    text-align: center;

    padding: 0;
    margin: 0 0 20px 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    font-size: 24px;
    line-height: 36px;
    font-weight: 600;
    text-align: center;

    padding: 0;
    margin: 0 0 20px 0;

    z-index: 1;
  }

  &::before {
    content: "";

    width: 175px;
    height: 3px;

    position: absolute;
    bottom: 0;
    left: 0;

    background: $accent;

    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      display: none;
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      display: none;
    }
  }
}

.price__info-text {
  font-size: 16px;
  line-height: 27.2px;
  font-weight: 400;

  color: $price-info-text-color;

  margin: 0 0 61px;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    text-align: center;

    padding: 0 21px;
    margin: 0 0 31px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    text-align: center;

    padding: 0;
    margin: 0 0 17px;
  }
}

.price__info-subtitle {
  font-size: 22px;
  line-height: 34px;
  font-weight: 500;

  color: $price-info-subtitle-color;

  padding: 0 0 0 45px;
  margin: 0 0 13px;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    font-size: 18px;
    text-align: center;

    padding: 0;
    margin: 0 0 10px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    font-size: 18px;
    text-align: center;

    padding: 0;
    margin: 0 0 11px;
  }
}

.price__info-phone {
  font-size: 20px;
  line-height: 32px;
  font-weight: 400;
  text-decoration: none;

  color: $price-info-phone-color;

  position: relative;

  padding: 0 0 0 46px;
  margin: 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    font-size: 18px;
    text-align: center;
    text-decoration: underline;

    display: block;

    color: $accent;

    padding: 0;
    margin: 0 0 40px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    font-size: 18px;
    text-align: center;
    text-decoration: underline;

    display: block;

    color: $accent;

    padding: 0;
    margin: 0 0 38px;
  }

  &::before {
    content: "";

    width: 30px;
    height: 30px;

    position: absolute;
    top: 0;
    left: 0;

    background: url("../img/price-phone.svg");

    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      display: none;
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      display: none;
    }
  }
}
