.popup-buy-tour {
  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  background: $popup-buy-tour-container-background;

  position: fixed;
  inset: 0;

  margin: auto;

  z-index: 1;
}

.popup-buy-tour--hide {
  display: none;
}

.popup-buy-tour__window {
  width: 728px;
  max-width: 100%;
  min-height: 444px;

  position: static;

  display: flex;

  background: url("../img/popup-buy-tour-desktop.png") no-repeat 0;
  background-size: 338px 100%;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 320px;

    background: none;
  }
}

.popup-buy-tour__form {
  width: 53.57%;

  display: flex;
  flex-flow: column;
  align-items: center;

  position: relative;

  background: $popup-buy-tour-form-background;

  padding: 72px 61px 89px 59px;
  margin: 0 0 0 auto;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    min-height: 568px;

    padding: 100px 16px 185px;
    margin: 0;
    box-sizing: border-box;
  }
}

.popup-buy-tour__form-title {
  font-family: inherit;
  font-size: 25px;
  line-height: 42.5px;
  font-weight: 600;
  text-align: center;

  color: $popup-buy-tour-text-color;

  margin: 0 0 50px;
}

.popup-buy-tour__form-input-phone {
  width: 270px;
  height: 50px;

  font-family: inherit;
  font-size: 14px;
  line-height: 17.07px;
  font-weight: 400;

  border: 1px solid $popup-buy-tour-border-color;

  color: $popup-buy-tour-text-color;

  padding: 16px 15px 15px 26px;
  margin: 0 0 20px;
  box-sizing: border-box;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
  }

  &:invalid {
    outline: 1px solid #fe7865;
  }
}

.popup-buy-tour__form-input-email {
  width: 270px;
  height: 50px;

  font-family: inherit;
  font-size: 14px;
  line-height: 17.07px;
  font-weight: 400;

  border: 1px solid $popup-buy-tour-border-color;

  color: $popup-buy-tour-text-color;

  padding: 16px 15px 15px;
  margin: 0 0 20px;
  box-sizing: border-box;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
  }

  &:invalid {
    outline: 1px solid #fe7865;
  }
}

.popup-buy-tour__form-button-push {
  min-width: 176px;
  max-width: 100%;
  min-height: 50px;

  font-family: inherit;
  font-size: 12px;
  line-height: 14.63px;
  font-weight: 700;
  letter-spacing: 2px;

  border: none;

  color: $button-text-color;
  background: $accent;

  padding: 18px 39px 17px 40px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    margin: 0 auto;
  }
}

.popup-buy-tour__form-button-close {
  width: 22px;
  height: 22px;

  position: absolute;
  top: 22px;
  left: calc(100% - 42px);

  background: transparent;

  border: none;

  padding: 0;
  margin: 0;
}

.popup-buy-tour__form-button-close svg {
  width: 22px;
  height: 22px;
}
