.contacts {
  width: 33.4%;

  display: flex;
  flex-flow: column;
  align-items: center;

  position: relative;

  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 45.73%;

    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    padding: 30px 0 24px;
    margin: 0;
  }

  &::before {
    content: "";

    width: 1px;
    height: 189px;

    position: absolute;
    top: 0;
    right: 0;

    background: $footer-border-color;

    @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
      display: none;
    }

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      width: 100%;
      height: 1px;

      position: absolute;
      top: 100%;
      left: 0;
    }
  }
}

.contacts__wrapper {
  width: 100%;

  display: flex;
  flex-flow: column;
}

.contacts__title {
  height: max-content;

  font-size: 18px;
  line-height: 32px;
  font-weight: 600;

  color: $footer-title-color;

  margin: 0 0 25px 16.62%;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    text-align: center;

    margin: 0 0 28px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    text-align: center;

    margin: 0 0 24px;
  }
}

.contacts__email {
  font-size: 14px;
  line-height: 17.07px;
  font-weight: 400;
  text-decoration: none;

  position: relative;

  color: $accent;

  padding: 0 0 0 41px;
  margin: 0 0 23px 16.62%;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 258px;

    padding: 0 0 0 46px;
    margin: 0 auto 24px;
    box-sizing: border-box;
  }

  &::before {
    content: "";

    width: 26px;
    height: 20px;

    position: absolute;
    top: 0;
    left: 0;

    background: url("../img/contacts-email.svg");
  }
}

.contacts__phone {
  font-size: 16px;
  line-height: 32px;
  font-weight: 400;

  position: relative;

  color: $accent;

  padding: 0 0 0 41px;
  margin: 0 0 0 16.62%;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 209px;

    padding: 0 0 0 41px;
    margin: 0 auto;
    box-sizing: border-box;
  }

  &::before {
    content: "";

    width: 30px;
    height: 30px;

    position: absolute;
    top: 0;
    left: 0;

    background: url("../img/contacts-phone.svg");
  }
}
