.callback {
  width: 61%;
  height: 455px;

  background: url("../img/callback-desktop.png") no-repeat;
  background-size: 844px 455px;

  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/callback-desktop@2x.png");
  }

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    background: url("../img/callback-tablet.png") no-repeat;
    background-size: cover;

    padding: 72px 55px 74px 54px;
    box-sizing: border-box;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/callback-tablet@2x.png");
    }
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    height: auto;

    background: none;

    padding: 25px 0 0 0;
    margin: 0;
    box-sizing: border-box;
  }
}

.callback__form {
  width: 656px;
  height: 315px;

  padding: 70px 0 0;
  margin: 0 0 0 auto;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
    height: auto;

    padding: 0;
    margin: 0 auto;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    height: auto;

    padding: 0;
    margin: 0;
  }
}

.callback__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  background: $form-background-color;

  border-radius: 5px;

  box-shadow: 0 4px 30px 0 $callback-shadow;

  padding: 50px 49px 49px 50px;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    display: flex;
    flex-flow: column;

    padding: 50px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
    display: flex;
    flex-flow: column;

    border-radius: 0;

    background: $form-background-color-mobile;

    box-shadow: none;
    padding: 50px 16px 50px;
    box-sizing: border-box;
  }
}

.callback__title {
  font-size: 25px;
  line-height: 32px;
  font-weight: 600;
  text-align: center;

  color: $form-title-color;

  display: block;

  padding: 0;
  margin: 0 auto 11px;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    margin: 0 auto 20px;
  }
}

.callback__text {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  text-align: center;

  color: $form-text-color;

  display: block;

  margin: 0 auto 24px;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    margin: 0 auto 20px;
  }
}

.callback__input-container {
  width: 100%;

  display: flex;
  justify-content: space-between;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }
}

.callback__label {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 48.21%;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
  }
}

.callback__input-phone {
  width: 269px;

  font-size: 14px;
  line-height: 17.07px;
  font-weight: 400;
  font-family: inherit;

  color: $form-input-color;

  border: 1px solid $callback-input-border;

  padding: 15px;
  margin: 0 0 23px;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    padding: 15px;
    margin: 0 0 19px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    padding: 15px;
    margin: 0 0 18px;
  }

  &:invalid {
    outline: 1px solid #fe7865;
    outline-offset: -1px;
  }
}


.callback__input-email {
  width: 269px;

  font-size: 14px;
  line-height: 17.07px;
  font-weight: 400;
  font-family: inherit;

  color: $form-input-color;

  border: 1px solid $callback-input-border;

  padding: 16px 15px 15px;
  margin: 0 0 23px;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    margin: 0 0 18px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    padding: 18px 15px 15px;
    margin: 0 0 19px;
  }

  &:focus {
    outline: 3px solid lightgreen;
    outline-offset: -3px;
  }
}

.callback__button {
  width: 176px;
  height: 50px;
  font-size: 12px;
  line-height: 14.63px;
  font-weight: 700;
  letter-spacing: 2px;
  text-align: center;
  font-family: inherit;

  color: $button-text-color;
  background: $accent;

  border: none;

  padding: 18px 39px 17px 40px;
  margin: 0 auto;
  box-sizing: border-box;

  cursor: pointer;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 250px;
    height: 50px;

    font-size: 10px;
    line-height: 12.19px;
    font-weight: 500;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 288px;

    margin: 0 auto;
    box-sizing: border-box;
  }

  &:focus {
    outline: 3px solid lightgreen;
    outline-offset: -3px;
  }
}

.callback__button:hover {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    background: $accent-hover;
  }
}
