.tab {
  width: 61%;

  padding: 62px 0 0;
  margin: 0 auto;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;

    overflow-x: scroll;
    scrollbar-width: none;

    padding: 40px 0 16px 21px;
    box-sizing: border-box;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 320px;

    overflow-x: scroll;
    scrollbar-width: none;

    padding: 20px 16px 23px;
    box-sizing: border-box;
  }
}

.tab__list {
  width: 100%;

  font-size: 20px;
  line-height: 24.38px;
  font-weight: 500;
  letter-spacing: -0.41px;
  list-style: none;
  text-align: center;

  display: flex;
  justify-content: space-between;

  position: relative;

  padding: 0 0 15px 0;
  margin: 0 auto 0 0;
  box-sizing: border-box;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 100%;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;
  }

  &::before {
    content: "";

    width: 100%;
    height: 1px;

    position: absolute;
    bottom: 0;
    left: 0;

    background: $place-navigation-line;
  }
}

.tab__item {
  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    margin: 0 13% 0 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    margin: 0 35% 0 0;
  }
}

.tab__item:last-child {
  margin: 0;
}

.tab__item:hover .tab__link {
  color: $accent;
}

.tab__link {
  text-decoration: none;

  color: $place-navigation-text;
}

.tab__link--current {
  color: $accent;
}
