.next-travels {
  width: 33.3%;

  position: relative;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 54.27%;

    margin: 0 0 50px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    position: relative;

    padding: 30px 0 30px;
    margin: 0;
  }

  &::before {
    content: "";

    width: 1px;
    height: 189px;

    position: absolute;
    top: 0;
    right: 0;

    background-color: $footer-border-color;

    @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
      width: 100%;
      height: 1px;

      top: 100%;
    }
  }
}

.next-travels__title {
  font-size: 18px;
  line-height: 32px;
  font-weight: 600;

  color: $footer-title-color;

  margin: 0 0 20px 0;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    text-align: center;

    margin: 0 0 22px 0;
  }
}

.next-travels__list {
  width: 100%;

  list-style: none;

  display: flex;
  flex-flow: row wrap;

  padding: 0;
  margin: 0;
}

.next-travels__list-left-column,
.next-travels__list-center-column,
.next-travels__list-right-column {
  list-style: none;

  padding: 0;
  margin: 0;
}

.next-travels__item {
  width: 27%;

  font-size: 14px;
  line-height: 17.07px;
  font-weight: 400;

  color: $footer-text-color;

  margin: 0 6% 10px 0;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    width: 27%;
    margin: 0 5% 0 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 50%;

    text-align: center;

    margin: 0 0 20px;
  }
}

.next-travels__item:last-child {
  margin: 0;

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    margin: 0;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    width: 100%;

    text-align: center;

    margin: 0;
  }
}

.next-travels__item-left-column,
.next-travels__item-center-column,
.next-travels__item-right-column {
  margin: 0 0 10px;

  @media (min-width: $tablet-width-min) and (max-width: $tablet-width-max) {
    margin: 0 0 10px;
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    margin: 0 0 10px;
  }
}

.next-travels__item-left-column:last-child,
.next-travels__item-center-column:last-child,
.next-travels__item-right-column:last-child {
  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    margin: 0;
  }
}
